@import '../../../constants.scss';

header.app-header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: var(--body-background);
  z-index: 1025;

  .content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: $max-width;
    margin: auto;
    padding: 15px 0;
    height: 80px;
    .left-side {
      position: relative;
      display: flex;
      align-items: center;
      .sk--logo-section {
        color: var(--app-text);
        text-decoration: none;
  
        .sk--square {
          width: 60px;
          height: 55px;
          border: 5px solid $theme-color;
          border-radius: 3px;
        }
  
        .sk--text {
          background-color: var(--body-background);
          padding: 6px;
          font-size: 28px;
          z-index: 1000000;
          position: absolute;
          top: 10px;
          left: -2px;
          font-weight: 600;
          font-weight: 600;
        }
      }
    }
  
    .right-side {
      display: flex;
      justify-content: flex-end;
      ul {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0;
        margin: 0;
        list-style-type: none;
  
        li {
          margin: 0 20px;
  
          a {
            color: var(--app-text);
            text-decoration: none;
            font-size: 16px;
            font-weight: 600;
          }
  
          &:hover {
            transform: scale(1.1);
  
            a {
              color: $theme-color;
            }
          }
  
          &:last-child {
            margin-right: 0;
          }
          
          i {
            color: var(--app-text);
            font-size: 24px;
            margin: 0 15px;
            cursor: pointer;
          }
        }

        li.active-link {
          a {
            color: $theme-color;
          }
        }
      }
    }
  }

}

@media screen and (max-width: 767px) {
  header.app-header {
    .content {
      padding: 20px;

      .right-side {
        .menu-icon {
          z-index: 1026;
          i {
            font-size: 32px;
            color: var(--app-text);
          }
        }
  
        ul {
          position: fixed;
          top: 0;
          left: 0;
          z-index: -1;
          width: 100%;
          height: 100vh;
          flex-direction: column;
          justify-content: center;
          align-items: center;
    
          li {
            margin: 20px 0;
    
            a {
              color: var(--app-text);
              text-decoration: none;
              font-size: 16px;
              font-weight: 600;
            }
    
            &:hover {
              transform: scale(1.1);
    
              a {
                color: $theme-color;
              }
            }
    
            &:last-child {
              margin-right: 0;
            }
            
            i {
              color: var(--app-text);
              font-size: 24px;
              margin: 0 15px;
              cursor: pointer;
            }
          }
  
          li.active-link {
            a {
              color: $theme-color;
            }
          }
        }
      }
    }
  }
}